@import url(https://fonts.googleapis.com/css2?family=Chathura&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 375px;
  /* height: 812px; */
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(236, 230, 230);
  position: relative;
  margin-top: 20px;
  margin-left: 480px;
}


.row{
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  margin-bottom: 20px;
}



.SpecialZero{
  width: 150px;
  height: 66px;
background: #212638;

display: flex;
/* align-items: center;
justify-content: center; */
flex: 1 1;

color: white;
border-radius: 35px;
font-size: 36px;
align-items: center;
text-align: left;
padding-left: 22px;
outline: none;
border: none;
cursor: pointer;
}


.Input_design{
  font-family: 'Chathura', sans-serif;
